.samples {
	-ms-flex: 1 1;
	    flex: 1 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.sample {
	position: relative;
	display: -ms-flexbox;
	display: flex;
	height: 80px;
	width: 80px;
	margin: 5px;
}

.sample-cover {
	height: 80px;
	width: 80px;
	opacity: 0.1;
	-webkit-transition: opacity 0.5s ease-in-out;
	-o-transition: opacity 0.5s ease-in-out;
	transition: opacity 0.5s ease-in-out;
}

.sample-title {
	display: none;
	position: absolute;
	top: -30px;
	background-color: #000;
	color: #FFF;
	padding: 5px;
	line-height: 20px;
	white-space: nowrap;
	text-decoration: none;
}

.sample-title:hover {
	text-decoration: underline;
}

.sample.playing .sample-cover {
	opacity: 1.0;
}

.sample:hover .sample-title {
	display: block;
}

.sample-info {
	position: absolute;
	top: 0;
	left: 0;
	background-color: #000;
	color: #FFF;
	display: inline;
	padding: 5px;
	min-width: 20px;
	text-align: center;
}

/* --------------------------------------------------------------- */

.samples .sample {
	height: 20vh;
	width: 20vh;
}

.samples .sample-cover {
	height: 20vh;
	width: 20vh;
}



